const wireSizes = [18, 16, 14, 12, 10, 8, 6, 4, 3, 2, 1, '1/0', '2/0', '3/0', '4/0'].map(x => x.toString());

export enum WireSizeComparison{
    SMALLER = 'smaller',
    EQUAL = 'equal',
    LARGER = 'larger'
}

export const compareWireSize = (size1: string) => ({
    to: (size2: string) => {
        const idx1 = wireSizes.indexOf(size1);
        const idx2 = wireSizes.indexOf(size2);
        if(idx1 < idx2) {
            return WireSizeComparison.SMALLER
        }
        if(idx1 === idx2) {
            return WireSizeComparison.EQUAL
        }
        if(idx1 > idx2) {
            return WireSizeComparison.LARGER
        }
    }
});